export default "import { Component } from \"@angular/core\";\n\n@Component({\n    selector: \"nui-button-repeatable-example\",\n    templateUrl: \"./button-repeatable.example.component.html\",\n})\nexport class ButtonRepeatableExampleComponent {\n    count = 100;\n\n    onCountUpClick() {\n        this.count++;\n    }\n\n    onCountDownClick() {\n        this.count--;\n    }\n}\n";